/* Container and Header Styling */
.projects {
    padding: 60px 20px;
    background-color: #1a1a2e;
    color: #fff;
}

.projects .container {
    max-width: 1200px;
    margin: 0 auto;
}

.projects h2 {
    text-align: center;
    font-size: 2.5rem;
    margin-bottom: 20px;
}

.projects p {
    text-align: center;
    font-size: 1rem;
    margin-bottom: 40px;
    color: #b0b0b0;
}

/* Card Layout */
.project-card {
    background-color: #22223b;
    border-radius: 8px;
    padding: 20px;
    margin: 20px;
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.2);
    transition: transform 0.2s ease, box-shadow 0.2s ease;
}

.project-card h3 {
    color: #25f7aa;
    font-size: 1.5rem;
    margin-bottom: 10px;
}

.project-card p {
    font-size: 1rem;
    line-height: 1.6;
}

.project-card strong {
    color: #ffd60a;
}

/* Hover Effect */
.project-card:hover {
    transform: translateY(-5px);
    box-shadow: 0 8px 16px rgba(0, 0, 0, 0.3);
}

/* Grid Layout */
.projects .container {
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(300px, 1fr));
    gap: 20px;
}

/* Responsive Design */
@media (max-width: 768px) {
    .projects h2 {
        font-size: 2rem;
    }

    .projects p {
        font-size: 0.9rem;
    }
}
