footer {
    background-color: #151538;
    padding: 2rem 0;
    text-align: center;
}

footer p {
    font-size: 1.2rem;
    margin-bottom: 1rem;
}

footer ul {
    list-style: none;
    display: flex;
    justify-content: center;
    margin-bottom: 1rem;
}

footer ul li {
    margin: 0 0.5rem;
}

footer ul li a {
    color: #fff;
    text-decoration: none;
}

footer .social-icons {
    margin-top: 1rem;
}

footer .social-icons a {
    color: #fff;
    margin: 0 0.5rem;
    font-size: 1.5rem;
}
