.experience {
    padding: 4rem 0;
    background-color: #0d0d2b;
    text-align: center;
}

.experience .timeline {
    display: flex;
    flex-direction: column;
    align-items: center;
}

.experience .timeline-item {
    background-color: #151538;
    padding: 1rem;
    margin: 1rem 0;
    border-radius: 8px;
    width: 80%;
}

.experience .timeline-item h3 {
    margin-bottom: 0.5rem;
}

.experience .timeline-item h4 {
    font-weight: normal;
    color: #7e22ce;
}

.experience .timeline-item p {
    margin-bottom: 0.5rem;
}
