/* Global Styles */
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  font-family: 'Roboto', sans-serif;
  background-color: #0d0d2b;
  color: #fff;
  line-height: 1.6;
  overflow-x: hidden;
}

.container {
  width: 80%;
  margin: auto;
  padding: 2rem 0;
}

/* Button Styles */
.btn {
  display: inline-block;
  padding: 0.5rem 1rem;
  margin: 0.5rem;
  border-radius: 4px;
  text-decoration: none;
  color: #fff;
  background-color: #3f0071;
  transition: background 0.3s ease;
}

.btn:hover {
  background-color: #7e22ce;
}

/* Primary Button */
.primary-btn {
  background-color: #7e22ce;
}

.primary-btn:hover {
  background-color: #a832e2;
}

.App {
  padding-top: 80px; /* Adjust this value to match the header height */
}