.about-section {
    max-width: 800px;          /* Limit the width for better readability */
    margin: 0 auto;            /* Center the section */
    padding: 20px;             /* Add padding around the text */
    text-align: left;          /* Align text to the left for a professional look */
    color: #ffffff;            /* Set text color to white */
  }
  
  .about-title {
    font-size: 2rem;           /* Larger font size for the title */
    margin-bottom: 20px;       /* Space below the title */
    text-align: center;        /* Center the title */
  }
  
  .about-paragraph {
    font-size: 1rem;           /* Standard font size for paragraphs */
    line-height: 1.6;          /* Increase line height for readability */
    margin-bottom: 15px;       /* Space between paragraphs */
    text-align: justify;       /* Justify text for a cleaner appearance */
  }
  