/* Skills Section Styling */
.skills {
    padding: 60px 20px;
    background-color: #1a1a2e;
    color: #ffffff;
}

.skills .container {
    max-width: 1200px;
    margin: 0 auto;
    text-align: center;
}

.skills h2 {
    font-size: 2.5rem;
    margin-bottom: 20px;
    color: #25f7aa;
}

.skills-wrapper {
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(150px, 1fr));
    gap: 20px;
    justify-content: center;
}

/* Set a higher minimum width for the skills-column to keep all items in one row */
@media (min-width: 1200px) {
    .skills-wrapper {
        grid-template-columns: repeat(8, 1fr); /* Adjust this number if you have more or fewer categories */
        gap: 10px;
    }
}

.skills-column {
    background-color: #22223b;
    border-radius: 8px;
    padding: 20px;
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.2);
}

.skills-column h3 {
    font-size: 1.3rem;
    color: #ffd60a;
    margin-bottom: 10px;
}

.skills-column ul {
    list-style: none;
    padding: 0;
}

.skills-column li {
    font-size: 1rem;
    line-height: 1.6;
    color: #b0b0b0;
}
