/* Header.css */

header {
    background-color: #151538;
    padding: 1rem;
    position: fixed;
    width: 100%;
    top: 0;
    z-index: 1000;
}

nav {
    display: flex;
    justify-content: space-between;
    align-items: center;
}

nav .logo {
    font-size: 1.5rem;
    font-weight: bold;
}

nav ul {
    display: flex;
    list-style: none;
}

nav ul li {
    margin-left: 1rem;
}

nav ul li a {
    color: #fff;
    text-decoration: none;
    padding: 0.5rem 1rem;
}

/* Button Styling */
.header-buttons .btn {
    margin-left: 0.5rem;
    padding: 0.5rem 1rem;
    color: #fff;
    background-color: #25f7aa;
    border-radius: 5px;
    text-decoration: none;
    transition: background-color 0.3s ease;
}

.header-buttons .btn:hover {
    background-color: #ffd60a;
}

/* Red-Green Gradient Button for Resume Download */
.resume-download {
    background: linear-gradient(45deg, #ff0000, #00ff00); /* Red-Green gradient */
    color: #ffffff;
    font-weight: bold;
    border-radius: 5px;
    text-decoration: none;
    padding: 0.5rem 1rem;
    transition: background 0.3s ease;
}

.resume-download:hover {
    background: linear-gradient(45deg, #00ff00, #ff0000); /* Reverse the gradient on hover */
}
