/* Publications.css */

.publications {
    padding: 60px 20px;
    background-color: #1a1a2e;
    color: #ffffff;
}

.publications .container {
    max-width: 1200px;
    margin: 0 auto;
    text-align: center;
}

.publications h2 {
    font-size: 2.5rem;
    margin-bottom: 20px;
    color: #25f7aa;
}

.publications p {
    font-size: 1rem;
    color: #b0b0b0;
    margin-bottom: 40px;
}

.publication-card {
    background-color: #22223b;
    border-radius: 8px;
    padding: 20px;
    margin: 20px 0;
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.2);
    text-align: left;
}

.publication-card h3 {
    color: #ffd60a;
    font-size: 1.5rem;
    margin-bottom: 10px;
}

.publication-card p {
    font-size: 1rem;
    line-height: 1.6;
}

.publication-card a {
    color: #25f7aa;
    text-decoration: underline;
}

.publication-card a:hover {
    color: #ffd60a;
}
