.contact {
    padding: 4rem 0;
    text-align: center;
    background-color: #0d0d2b;
}

.contact h2 {
    margin-bottom: 1rem;
}

.contact form {
    max-width: 400px;
    margin: 0 auto;
    display: flex;
    flex-direction: column;
}

.contact form input, .contact form textarea {
    padding: 0.5rem;
    margin-bottom: 1rem;
    border-radius: 4px;
    border: none;
    background-color: #151538;
    color: #fff;
}

.contact form button {
    padding: 0.5rem;
    border: none;
    background-color: #7e22ce;
    color: #fff;
    cursor: pointer;
    border-radius: 4px;
    transition: background 0.3s ease;
}

.contact form button:hover {
    background-color: #a832e2;
}
